.order {
  width: 100%;

  line-height: 24px;
  font-weight: 500;

  color: #8A94A6;

  padding: 8px 16px 0;
  margin: 0 0 40px;

  @include desktop {
    padding: 56px 52px 0;
  }
}

.order__preview {
  font-weight: 700;
  text-transform: uppercase;

  margin: 0 0 16px;

  @include desktop {
    margin: 0 0 24px;
  }
}

.order__title {
  font-size: 32px;
  line-height: 32px;

  color: #192B3F;

  margin: 0 0 16px;

  @include tablet {
    font-size: 48px;
    line-height: 56px;

    margin: 0 0 12px;
  }
}

.order__description {
  font-size: 18px;
  line-height: 28px;

  position: relative;

  padding: 0 0 16px;
  margin: 0 0 17px;

  opacity: 80%;

  &::before {
    content: '';

    width: 100%;
    height: 1px;

    position: absolute;
    left: 0;
    bottom: 0;

    background: #E1E6ED;

    @include desktop {
      min-width: 616px;
    }
  }

  @include tablet {
    font-size: 20px;
    line-height: 32px;

    padding: 0 0 7px;
    margin: 0 0 26px;
  }

  @include desktop {
    width: 62.18%;

    padding: 0 0 12px;
    margin: 0 0 17px;
  }
}

.order__form {
  margin: 0 0 40px;

  @include tablet {
    display: grid;
    grid-template-columns: 21.51% 1fr;
    gap: 0 10.47%;
  }

  @include desktop {
    display: grid;
    grid-template-columns: 21.51% 1fr;
    gap: 0 4.71%;

    margin: 0 0 32px;
  }
}

.order__form-color {
  margin: 0 0 24px;

  @include desktop {
    margin: 0;
  }
}

.order__form-color-legend {
  font-weight: 700;
  text-transform: uppercase;

  margin: 0 0 8px;

  @include tablet {
    margin: 0 0 6px;
  }

  @include desktop {
    margin: 0 0 10px;
  }
}

.order__form-color-container {
  width: 152px;

  display: flex;
  justify-content: space-between;

  @include tablet {
    width: 152px;
  }

  @include desktop {
    width: 105px;

    padding: 0 0 0 3px;
  }
}

.order__form-color-label {
  width: 48px;
  height: 48px;

  display: block;

  position: relative;

  @include desktop {
    width: 26px;
    height: 26px;
  }
}

.order__form-color-input {
  width: 48px;
  height: 48px;

  appearance: none;

  margin: 0;

  @include desktop {
    width: 26px;
    height: 26px;
  }
}

.order__form-color-span {
  content: '';

  width: 40px;
  height: 40px;

  display: block;

  position: absolute;
  top: 0;
  left: 0;

  border-radius: 50%;

  background: #192B3F;

  margin: 3px 0 0 4px;

  @include tablet {
    margin: 5px 0 0 4px;
  }

  @include desktop {
    width: 26px;
    height: 26px;

    margin: 0;
  }
}

.order__form-color-span--second {
  background: #8A94A6;
}

.order__form-color-span--third {
  background: #3D5876;
}

.order__form-color-input:checked + .order__form-color-span::before {
  content: '';

  width: 48px;
  height: 48px;

  position: absolute;
  top: -4px;
  left: -4px;

  background: url('../img/radio-checked.svg');

  @include desktop {
    width: 32px;
    height: 32px;

    top: -3px;
    left: -3px;

    background: url('../img/radio-checked-desktop.svg');
  }
}

.order__form-size {
  margin: 0 0 33px;

  @include tablet {
    display: flex;
    align-items: center;
  }

  @include desktop {
    margin: 0 0 32px;
  }
}

.order__form-size-legend {
  font-weight: 700;
  text-transform: uppercase;

  margin: 0 0 12px;

  @include tablet {
    margin: 0;
  }

  @include desktop {
    margin: 0 0 9px;
  }
}

.order__form-size-container {
  width: 76.04%;
  min-height: 24px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  margin: 0 0 31px;

  @include tablet {
    width: 216px;

    margin: 0 43px 0 0;
  }

  @include desktop {
    width: 165px;

    padding: 0 0 0 3px;
    margin: 0 31px 0 0;
  }
}

.order__form-size-label {
  display: block;

  position: relative;
}

.order__form-size-input {
  font-weight: 700;

  appearance: none;

  margin: 0;

  &:focus {
    position: relative;
    outline: none;
  }
  &:focus::before {
    content: '';

    width: 32px;
    height: 32px;

    position: absolute;
    top: -22px;
    left: -6px;
  }
}

.order__form-size-span {
  position: relative;

  font-size: 16px;
}

.order__form-size-span--disabled {
  opacity: 50%;
}

.order__form-size-input:checked + .order__form-size-span {
  font-size: 28px;

  color: #192B3F;

  @include desktop {
    font-size: 20px;
  }
}

.order__form-size-input:checked + .order__form-size-span::before {
  content: '';

  width: 48px;
  height: 48px;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  background: url('../img/radio-checked.svg');

  @include tablet {
    top: 55%;
  }

  @include desktop {
    width: 32px;
    height: 32px;

    font-size: 20px;

    top: 51%;

    background: url('../img/radio-checked-desktop.svg');
  }
}

.order__form-size-button-guide {
  width: 92px;
  height: 28px;

  font-family: inherit;
  font-size: 16px;
  text-align: center;

  border: 1px solid #C9B8A8;
  border-radius: 24px;

  color: #C9B8A8;
  background: transparent;

  padding: 2px 0;

  @include tablet {
    padding: 0;
  }
}

.order__form-container-goods {
  width: 90.28%;

  display: flex;
  justify-content: space-between;

  position: relative;

  padding: 0 0 21px;
  margin: 0 0 29px;

  &::before {
    content: '';

    width: 100%;
    min-width: 288px;
    height: 1px;

    position: absolute;
    left: 0;
    bottom: 0;

    background: #E1E6ED;
  }

  @include tablet {
    width: 100%;

    grid-column: 1 / -1;
    justify-content: flex-start;

    padding: 14px 0 20px;
    margin: 0 0 18px;
  }

  @include desktop {
    padding: 3px 0 22px;
    margin: 0 0 25px;
  }
}

.order__form-quantity {
  width: 78px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  @include tablet {
    width: 11.34%;

    margin: 0 20.64% 0 0;
  }

  @include desktop {
    width: 77px;

    margin: 0 84px 0 0;
  }
}

.order__form-quantity-legend {
  font-weight: 700;
  text-transform: uppercase;

  margin: 0 0 4px;
}

.order__form-quantity-button {
  width: 16px;
  height: 16px;

  border: none;

  background: transparent;

  padding: 0;
}

.order__form-quantity-input {
  width: 24px;

  font-size: 24px;
  text-align: center;

  color: #8A94A6;

  border: none;

  padding: 0;
}

.order__form-cost {
  width: 108px;
}

.order__form-cost-text {
  font-size: 30px;
}

.order__form-cost-legend {
  font-weight: 700;
  text-transform: uppercase;

  margin: 0 0 5px;
}

.order__form-text {
  font-size: 30px;

  color: #8A94A6;
}

.order__form-cost-price--big {
  font-size: 40px;
  line-height: 40px;
  color: #C9B8A8;
}

.order__form-details {
  @include tablet {
    grid-column: 1 / -1;
  }
}

.order__form-details-title {
  font-weight: 700;

  margin: 0 0 6px;

  @include tablet {
    margin: 0 0 8px;
  }

  @include desktop {
    margin: 0 0 6px;
  }
}

.order__form-details-description {
  color: #192B3F;

  opacity: 80%;

  margin: 0 0 9px;

  @include tablet {
    margin: 0 0 8px;
  }

  @include desktop {
    margin: 0 0 10px;
  }
}

.order__form-details-structure {
  line-height: 20px;

  display: block;

  margin: 0 0 41px;

  @include tablet {
    margin: 0 0 40px;
  }

  @include desktop {
    margin: 0 0 71px;
  }
}

.order__form-submit-button {
  width: 288px;
  height: 56px;

  font-family: inherit;
  font-size: 24px;
  line-height: 32px;

  display: block;

  color: #ffffff;
  background: #192B3F;

  border: none;

  padding: 0;
  margin: 0 auto;

  @include tablet {
    width: 100%;

    grid-column: 1 / -1;
  }

  &:hover {
    background: #2E4055;
  }

  :active {
    background: #3B536D;
  }
}

.order__delivery,
.order__return {
  width: 100%;

  position: relative;

  padding: 0 0 8px;
  margin: 0 0 16px;

  &::before {
    content: '';

    width: 100%;
    height: 1px;

    position: absolute;
    left: 0;
    bottom: 0;

    background: #E1E6ED;
  }
}

.order__delivery-button-more ,
.order__return-button-more {
  width: 100%;
  height: 32px;

  font-family: inherit;
  font-size: 24px;
  line-height: 32px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  border: none;

  color: #192B3F;
  background: transparent;

  padding: 0 3px 0 0;

  @include desktop {
    padding: 0 1px 0 0;
  }
}

.order__delivery-svg-plus {
  width: 10px;
  height: 10px;
}

.order__delivery-description {
  line-height: 24px;

  margin: 16px 0;
}

.order__return {
  margin: 0;
}

.order__return-description {
  margin: 16px 0 0;
}
