.subscribe {
  width: 100%;

  padding: 0 16px 16px;

  @include tablet {
    padding: 0 16px 29px;
  }

  @include desktop {
    padding: 0 52px;
  }
}

.subscribe__title {
  font-size: 24px;
  line-height: 32px;
  font-weight: 500;

  color: #192B3F;

  margin: 0 0 8px;

  @include tablet {
    font-size: 32px;
    line-height: 40px;

    margin: 0;
  }
}

.subscribe__description {
  font-size: 18px;
  line-height: 28px;
  font-weight: 500;

  color: #8A94A6;

  margin: 0 0 28px;

  @include tablet {
    font-size: 20px;
    line-height: 32px;

    margin: 0 0 24px;
  }
}

.subscribe__form-input-text,
.subscribe__form-input-email {
  width: 100%;
  height: 44px;

  font-family: inherit;
  font-size: 16px;
  line-height: 40px;
  font-weight: 500;

  color: #8A94A6;

  border: none;

  background: #E1E6ED;

  padding: 0 0 0 16px;
  margin: 0 0 16px;
  box-sizing: border-box;

  @include tablet {
    width: 66.28%;
  }

  @include desktop {
    width: 73.79%;
  }
}

.subscribe__form-input-email {
  margin: 0 0 12px;
}

.subscribe__form-gender {
  display: flex;
  flex-flow: row wrap;
  align-items: center;

  margin: 0 0 15px;

  @include tablet {
    width: 100%;

    margin: 0 0 23px;
  }
}

.subscribe__form-gender-title {
  width: 100%;

  font-size: 20px;
  line-height: 32px;
  font-weight: 500;

  color: #8A94A6;

  margin: 0 0 4px;

  @include tablet {
    width: max-content;

    margin: 0 3.92% 0 0;
  }

  @include desktop {
    width: max-content;

    margin: 0 4.21% 0 0;
  }
}

.subscribe__form-gender-male-container,
.subscribe__form-gender-female-container {
  display: flex;
  flex-flow: row wrap;
  align-items: center;

  @include tablet {
    margin: 0 3.49% 0 0;
  }
}

.subscribe__form-gender-male-container {
  margin: 0 22% 0 0;

  @include tablet {
    margin: 0 9.16% 0 0;
  }

  @include desktop {
    margin: 0 10.19% 0 0;
  }
}

.subscribe__form-gender-label {
  color: #192B3F;

  position: relative;

  opacity: 80%;
}

.subscribe__form-gender-input {
  width: 20px;
  height: 20px;

  appearance: none;

  position: relative;

  margin: 0;

  &:focus {
    outline:  none;
  }

  &:focus::before {
    content: '';

    width: 20px;
    height: 20px;

    position: absolute;
    top: 3px;
    left: 0;

    border-radius: 50%;
    box-sizing: border-box;
    outline: 2px solid #E1E6ED;
    outline-offset: -2px;
  }
}

.subscribe__form-gender-sex {
  line-height: 24px;
  font-weight: 500;

  display: inline-block;

  position: absolute;
  top: 2px;
  left: 0;

  padding: 0 0 0 28px;

  &::before {
    content: '';

    width: 20px;
    height: 20px;

    position: absolute;
    top: 1px;
    left: 0;

    border: 1px solid #E1E6ED;
    border-radius: 50%;
    box-sizing: border-box;
  }

  @include tablet {
    padding: 1px 0 0 28px;
  }

  @include desktop {
    padding: 0 0 0 28px;
  }
}

.subscribe__form-gender-input:checked + .subscribe__form-gender-sex::after {
  content: '';

  width: 12px;
  height: 12px;

  display: block;

  position: absolute;
  top: 4px;
  left: 4px;

  background: #192B3F;
  border-radius: 50%;

  @include desktop {
    top: 5px;
  }
}


.subscribe__form-checkbox-container {
  margin: 0 0 17px;

  @include desktop {
    margin: 0 0 18px;
  }
}

.subscribe__form-checkbox {
  appearance: none;

  position: relative;

  margin: 0;

  &:focus {
    outline: none;
  }

  &:focus::before {
    content: '';

    width: 20px;
    height: 20px;

    position: absolute;
    top: 5px;
    left: 0;

    border: 1px solid #192B3F;
    box-sizing: border-box;

    z-index: 1;
  }
}

.subscribe__form-checkbox-description {
  line-height: 24px;

  display: block;

  position: relative;

  padding: 0 0 0 28px;

  &::before {
    content: '';

    width: 20px;
    height: 20px;

    position: absolute;
    top: 1px;
    left: 0;

    border: 1px solid #E1E6ED;
    box-sizing: border-box;
  }
}

.subscribe__form-checkbox:checked + span::before {
  border: none;
}

.subscribe__form-checkbox-container input:checked + .subscribe__form-checkbox-description::after {
  content: '';

  width: 20px;
  height: 20px;

  position: absolute;
  top: 1px;
  left: 0;

  border: none;

  background: #192B3F url('../img/checkbox-checked.svg') no-repeat 50% 50%;

  padding: 0;

  @include tablet {
    left: 1px;
  }

  @include desktop {
    top: 2px;
    left: 0;
  }
}

.subscribe__form-submit-button {
  width: 288px;
  height: 56px;

  font-family: inherit;
  font-size: 24px;
  line-height: 32px;
  font-weight: 500;
  text-align: center;

  display: block;

  border: none;

  color: #ffffff;
  background: #192B3F;

  padding: 0;
  margin: 0 auto;

  @include tablet {
    width: 100%;
  }
}
