.header {
  width: 100%;
  min-height: 600px;

  color: #ffffff;
  background: #cfbdaf url('../img/header-first-slide-mobile.png') no-repeat;
  background-size: cover;

  box-sizing: border-box;

  @include retina {
    background: #cfbdaf url('../img/header-first-slide-mobile@2x.png') no-repeat;
    background-size: cover;
  }

  @include tablet {
    background: #cfbdaf url('../img/header-first-slide-tablet.png') no-repeat;
    background-size: cover;

    @include retina {
      background: #cfbdaf url('../img/header-first-slide-tablet@2x.png') no-repeat;
      background-size: cover;
    }
  }

  @include desktop {
    width: 100%;
    height: 768px;

    grid-column: 1 / 2;

    background: #cfbdaf url('../img/header-first-slide-desktop.png') no-repeat;
    background-size: 600px 768px;

    @include retina {
      background: #cfbdaf url('../img/header-first-slide-desktop@2x.png') no-repeat;
      background-size: 600px 768px;
    }
  }
}

.header__wrapper {
  width: 100%;
  min-height: 600px;

  padding: 24px 16px 27px;
  box-sizing: border-box;

  @include tablet {
    min-height: 602px;
  }

  @include desktop {
    width: 600px;
    min-height: 768px;

    padding: 32px 52px;
  }
}

.header__logo {
  width: 65px;
  height: 36px;

  margin: 0 0 373px;

  @include tablet {
    margin: 0 0 357px;
  }

  @include desktop {
    width: 94.46px;
    height: 53px;

    margin: 0 0 479px;
  }
}

.header__title {
  font-size: 48px;
  line-height: 48px;

  margin: 0 0 5px;

  @include tablet {
    font-size: 64px;
    line-height: 64px;

    margin: 0;
  }

  @include desktop {
    font-size: 72px;
    line-height: 72px;
  }
}

.header__description {
  font-weight: 500;

  margin: 0 0 34px;

  opacity: 80%;

  @include tablet {
    font-size: 24px;
    line-height: 32px;

    margin: 0 0 24px;
  }

  @include desktop {
    font-size: 28px;

    margin: 0 0 32px;
  }
}

.header__slider {
  width: 79.86%;

  display: flex;
  justify-content: space-between;
  align-items: center;

  @include tablet {
    width: 33.43%;
  }

  @include desktop {
    width: 46.37%;
  }
}

.header__slider-button-container {
  width: 81px;

  display: flex;
  justify-content: space-between;
  align-items: center;

}

.header__slider-button-prevous,
.header__slider-button-next {
  width: 32px;
  height: 32px;

  display: flex;
  justify-content: center;
  align-items: center;

  background: transparent;

  border: none;

  padding: 0;
}

.header__slider-button-prevous:hover,
.header__slider-button-next:hover {
  border: 1px solid #ffffff;
  border-radius: 50%;
  box-sizing: border-box;
}

.header__slider-button-prevous:active,
.header__slider-button-next:active {
  border-radius: 50%;

  background: #ffffff;
}

.header__slider-button-prevous:active path,
.header__slider-button-next:active path {
  fill: #8A94A6;
}

.header__slider-slides-container {
  width: 70px;

  font-size: 20px;
  line-height: 32px;
  font-weight: 500;

  display: flex;
  justify-content: space-between;
  align-items: center;

  position: relative;

  &::before {
    content: '';

    width: 16px;
    height: 1px;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%);

    background: #ffffff;
  }
}

.header__slider-slide-max {
  opacity: 50%;
}
