@font-face {
  font-family: "Rajdhani";
  src: url("../fonts/Rajdhani-Regular.woff2") format("woff2"), url("../fonts/Rajdhani-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Rajdhani";
  src: url("../fonts/Rajdhani-Regular.woff2") format("woff2"), url("../fonts/Rajdhani-Regular.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Rajdhani";
  src: url("../fonts/Rajdhani-Bold.woff2") format("woff2"), url("../fonts/Rajdhani-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.html {
  height: 100%;
  font-family: "Rajdhani", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  font-style: normal;
  margin: 0 auto;
}

.body {
  height: 100%;
  display: flex;
  flex-flow: column;
}
@media (min-width: 1360px) {
  .body {
    width: 1360px;
    height: 100vh;
    display: grid;
    grid-template-columns: 600px 720px;
    align-content: center;
    gap: 0 40px;
    margin: auto;
  }
}

@media (min-width: 1360px) {
  .main {
    height: 768px;
    overflow-y: scroll;
    scrollbar-width: none;
  }
}
.main::-webkit-scrollbar {
  width: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}

button {
  cursor: pointer;
}

.hide {
  display: none;
}

.header {
  width: 100%;
  min-height: 600px;
  color: #ffffff;
  background: #cfbdaf url("../img/header-first-slide-mobile.png") no-repeat;
  background-size: cover;
  box-sizing: border-box;
}
@media (min-resolution: 2dppx) {
  .header {
    background: #cfbdaf url("../img/header-first-slide-mobile@2x.png") no-repeat;
    background-size: cover;
  }
}
@media (min-width: 720px) {
  .header {
    background: #cfbdaf url("../img/header-first-slide-tablet.png") no-repeat;
    background-size: cover;
  }
}
@media (min-width: 720px) and (min-resolution: 2dppx) {
  .header {
    background: #cfbdaf url("../img/header-first-slide-tablet@2x.png") no-repeat;
    background-size: cover;
  }
}
@media (min-width: 1360px) {
  .header {
    width: 100%;
    height: 768px;
    grid-column: 1/2;
    background: #cfbdaf url("../img/header-first-slide-desktop.png") no-repeat;
    background-size: 600px 768px;
  }
}
@media (min-width: 1360px) and (min-resolution: 2dppx) {
  .header {
    background: #cfbdaf url("../img/header-first-slide-desktop@2x.png") no-repeat;
    background-size: 600px 768px;
  }
}

