}

.header__slider-button-container {
  width: 81px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__slider-button-prevous,
.header__slider-button-next {
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  border: none;
  padding: 0;
}

.header__slider-button-prevous:hover,
.header__slider-button-next:hover {
  border: 1px solid #ffffff;
  border-radius: 50%;
  box-sizing: border-box;
}

.header__slider-button-prevous:active,
.header__slider-button-next:active {
  border-radius: 50%;
  background: #ffffff;
}

.header__slider-button-prevous:active path,
.header__slider-button-next:active path {
  fill: #8A94A6;
}

.header__slider-slides-container {
  width: 70px;
  font-size: 20px;
  line-height: 32px;
  font-weight: 500;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.header__slider-slides-container::before {
  content: "";
  width: 16px;
  height: 1px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%);
  background: #ffffff;
}

.header__slider-slide-max {
  opacity: 50%;
}

