@include font("Rajdhani", "Rajdhani-Regular", 400);
@include font("Rajdhani", "Rajdhani-Regular", 500);
@include font("Rajdhani", "Rajdhani-Bold", 700);

.html {
  height: 100%;

  font-family: "Rajdhani", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  font-style: normal;

  margin: 0 auto;
}

.body {
  height: 100%;

  display: flex;
  flex-flow: column;

  @include desktop {
    width: 1360px;
    height: 100vh;

    display: grid;
    grid-template-columns: 600px 720px;
    align-content: center;
    gap: 0 40px;

    margin: auto;
  }
}

.main {
  @include desktop {
    height: 768px;
    overflow-y: scroll;
    scrollbar-width: none;
  }
  &::-webkit-scrollbar {
    width: 0;
  }
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;

  border: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;

  padding: 0;
  margin: -1px;
}

button {
  cursor: pointer;
}

.hide {
  display: none;
}
