.collection {
  width: 100%;

  padding: 0 16px;
  margin: 0 0 52px;

  @include tablet {
    margin: 0 0 48px;
  }

  @include desktop {
    padding: 0 52px;
    margin: 0 0 63px;
  }
}

.collection__title {
  line-height: 24px;
  font-weight: 700;
  text-transform: uppercase;

  color: #8A94A6;

  margin: 0 0 8px;
}

.collection__list {
  width: 100%;

  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr;
  row-gap: 16px;

  @include tablet {
    row-gap: 28px;
  }

  @include desktop {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 160px;
    gap: 0 3.9%;
  }
}

.collection__figure {
  position: relative;
}

.collection__figure-description {
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;

  position: absolute;
  left: 5.56%;
  bottom: 12px;

  color: #ffffff;

  @include tablet {
    font-size: 40px;
    line-height: 40px;

    left: 2.47%;
    bottom: 18px;
  }

  @include desktop {
    font-size: 24px;
    line-height: 24px;

    left: 5.41%;
    bottom: 10px;
  }
}

.collection__figure-image {
  width: 100%;
  height: auto;

  display: block;

  object-fit: contain;

  margin: 0 auto;

  @include tablet {
    border-radius: 5px;
  }
}

.collection__figure-description-more {
  font-size: 14px;
  line-height: 16px;
  display: block;

  opacity: 50%;

  @include tablet {
    font-size: 18px;
    line-height: 18px;
  }

  @include desktop {
    font-size: 14px;
    line-height: 16px;
  }
}
